// 引入vue
import muiUtils from 'common/muiUtils';
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header';
import CONSTS from 'common/consts'
Vue.use(VueRouter);
var router = new VueRouter();

//头部head start
var HeaderVue = Vue.extend(vheader);
var header = new HeaderVue({
    propsData: {
        typeclass: 'wantBuy',
        hdstyle: {
            back: {
                display: true
            },
            searchBar: {
                display: true,
                placeholder: '搜索求购信息',
                modelName: CONSTS.PREFIX_BUYINGHALL,
            },
            rightButton: {
                display: 'none',
                text: '我的求购',
                onClick: function() {
					muiUtils.openWindow('../../buyer/purchase/list.html', 'buyer_purchase_list.html', {
						isValidLogin: true
					});
                }
            }
        },
        searchValue: '',
        value: ''
    },
    el: 'vheader',
    watch: {
        searchValue: function() {
            router.app.searchValue = this.searchValue;
        }
    }
});
//头部head end
mui.init({
    swipeBack: true
});
mui.plusReady(function() {
    var fromSellerPage = plus.webview.currentWebview().fromSellerPage;
    var searchValue = plus.webview.currentWebview().searchValue ? plus.webview.currentWebview().searchValue : '';
    if(fromSellerPage) {
        header.hdstyle.rightButton.display = 'none';
        header.typeclass = 'commodity';
    }
    else {
        header.hdstyle.rightButton.display = true;
        header.typeclass = 'wantBuy';
    }
    if(searchValue) {
        header.searchValue = searchValue;
        header.value = searchValue;
    }
    router.start(app, '#app');
});

window.addEventListener(CONSTS.WEBVIEW_SHOW_EVENT, function(e) {
    var searchValue = e.detail.searchValue ? e.detail.searchValue : '';
    if(searchValue) {
        header.searchValue = searchValue;
        header.value = searchValue;
    }
    else {
        header.searchValue = '';
        header.value = '';
    }
});

window.addEventListener('backbuyinghall', function(e) {
    header.searchValue = '';
    header.value = '';
});
